import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import Axios from "axios";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import { gagal } from "../../Alert";
import PrintNota from "../../Stocking/Component/PrintNota";
import HeaderCard from "../../HeaderCard";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormLaporanTerimaMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
        {
          dataField: "rp",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.getDetails(row.noFaktur)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      hasilDataGet: [],
      jenisMasak: "KIRIM MASAK",
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  getDetails(faktur) {
    if (this.state.jenisMasak === "KIRIM MASAK") {
      Axios.get(
        serverNonMaster + "item/dc/kirimmasak/detail?noFaktur=" + faktur
      )
        .then((res) => this.setLocal(res.data))
        .catch(() =>
          gagal("Sepertinya ada kesalahan, Mohon Periksa Koneksi anda")
        );
    } else {
      Axios.get(
        serverNonMaster + "item/dc/terimamasak/detail?noFaktur=" + faktur
      )
        .then((res) => this.setLocal(res.data))
        .catch(() =>
          gagal("Sepertinya ada kesalahan, Mohon Periksa Koneksi anda")
        );
    }
  }
  setLocal(data) {
    localStorage.setItem("PrintMasak", JSON.stringify(data.barangDetails));
    this.print(data.tanggal, data.noBon);
  }
  print(tanggal, noBon) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("PrintMasak"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        this.state.jenisMasak === "KIRIM MASAK"
          ? ticket.bruto
          : ticket.brutoTerima,
        ticket.kadarTransaksi,
        this.state.jenisMasak === "KIRIM MASAK"
          ? ticket.netto
          : ticket.nettoTerima,
        this.state.jenisMasak === "KIRIM MASAK" ? null : ticket.nettoSelisih,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var sumBruto = table
      .map((list) =>
        this.state.jenisMasak === "KIRIM MASAK" ? list.bruto : list.brutoTerima
      )
      .reduce((a, b) => a + b);
    var sumNettoTerima = table
      .map((list) =>
        this.state.jenisMasak === "KIRIM MASAK" ? list.netto : list.nettoTerima
      )
      .reduce((a, b) => a + b);
    var sumNettoSelisih = table
      .map((list) => list.nettoSelisih)
      .reduce((a, b) => a + b);
    const sum = [
      "",
      "",
      parseFloat(sumBruto).toFixed(3),
      "",
      parseFloat(sumNettoTerima).toFixed(3),
      this.state.jenisMasak === "KIRIM MASAK"
        ? null
        : parseFloat(sumNettoSelisih).toFixed(3),
    ];
    tableRows.push(sum);
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Jenis",
      "Bruto(Gr)",
      "Kadar",
      "Netto(Gr)",
      "Selisih(Gr)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "Tanggal",
      tanggal,
      "",
      "",
      "No Faktur",
      noBon,
      "",
      "",
      "ADMIN",
      tanggal,
      "",
      columTable,
      this.state.jenisMasak === "TERIMA MASAK"
        ? "BUKTI TERIMA MASAK"
        : "BUKTI KIRIM MASAK",
      tableRows
    );
  }
  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  caridata() {
    console.log("PENCARIAN....");
    if (this.state.jenisMasak === "KIRIM MASAK") {
      Axios.get(serverNonMaster + "item/dc/kirimmasak/heads", {
        params: {
          tglFrom: this.state.tglFrom,
          tglTo: this.state.tglTo,
          kodeSupplier: this.state.kodeSupplier,
        },
      })
        .then((res) =>
          this.setState({
            hasilDataGet: res.data,
          })
        )
        .catch(() => gagal("Data Kosong"));
    } else {
      Axios.get(serverNonMaster + "item/dc/terimamasak/heads", {
        params: {
          tglFrom: this.state.tglFrom,
          tglTo: this.state.tglTo,
          kodeSupplier: this.state.kodeSupplier,
        },
      })
        .then((res) =>
          this.setState({
            hasilDataGet: res.data,
          })
        )
        .catch(() => gagal("Data Kosong"));
    }
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Masak" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Tanggal Awal</label>
                  <input
                    type="date"
                    id="tanggal"
                    name="tanggalAwal"
                    className="form-control"
                    defaultValue={this.state.startingDate}
                    onChange={this.inputChange("tglFrom")}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="text-center">
                    <i className="fas fa-exchange-alt"></i>
                  </div>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Tanggal Akhir</label>
                  <input
                    type="date"
                    id="tanggal"
                    name="tanggalAkhir"
                    className="form-control"
                    defaultValue={this.state.startingDate}
                    onChange={this.inputChange("tglTo")}
                  />
                </div>
                <div className="col-md-4 col-lg-4 mt-3">
                  <label htmlFor="">Jenis Masak</label>
                  <select
                    id="jenisMasak"
                    name="jenisMasak"
                    className="form-control"
                    onChange={this.inputChange("jenisMasak")}
                  >
                    <option value="KIRIM MASAK" key="KIRIM MASAK">
                      KIRIM MASAK
                    </option>
                    <option value="TERIMA MASAK" key="TERIMA MASAK">
                      TERIMA MASAK
                    </option>
                  </select>
                </div>
                <div className="col-md-12 col-lg-12 mt-3">
                  <div className="row">
                    <div className="col-lg-6 col-md-6">
                      <div className="text-left">
                        {/* <Link to="/ViewPDF">
                              <button className="btn btn-primary" type="button">
                                PDF
                              </button>
                            </Link> */}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.caridata()}
                        >
                          <i className="fa fa-search mr-1"></i>
                          Cari Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      {this.state.hasilDataGet ? (
                        <ToolkitProvider
                          bootstrap4
                          keyField={this.state.hasilDataGet}
                          data={this.state.hasilDataGet}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="col-lg-12 col-md-12 mt-5">
                              <div className="text-right">
                                <SearchBar
                                  {...props.searchProps}
                                  className="form-control"
                                  placeholder="Tuliskan Kode / Nama"
                                  style={{ margin: 0 }}
                                />
                              </div>
                              <BootstrapTable
                                {...props.baseProps}
                                headerClasses="header-table"
                                striped
                                hover
                                bordered={true}
                                pagination={paginationFactory()}
                              />
                              <ExportCSVButton
                                {...props.csvProps}
                                className="btn btn-primary"
                              >
                                Export CSV
                              </ExportCSVButton>
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <div>
                          {this.props.error ? (
                            <div className="text-center">
                              <img
                                src={notrespond}
                                style={{ width: "30%" }}
                                alt="NOT RESPOND"
                              />
                              <br />
                              <h4>Request API Gagal, Silahkan Refresh</h4>
                            </div>
                          ) : (
                            <Skeleton width="100%" height={180} />
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanTerimaMasak);
